function sliderMain() {
    var swiper = new Swiper('.main .swiper', {
        slidesPerView: '1',
        loop: true,
        effect: 'fade',
        fadeEffect: {
            crossFade: true
        },
        navigation: {
            nextEl: '.main .swiper-button-next',
            prevEl: '.main .swiper-button-prev',
        },
        autoplay: {
            delay: 2500,
            disableOnInteraction: false
        },
    })
}

function sliderProducts() {
    var swiper = new Swiper('.products .swiper', {
        slidesPerView: '3',
        spaceBetween: 70,
        navigation: {
            nextEl: '.products .swiper-button-next',
            prevEl: '.products .swiper-button-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: '1',
                spaceBetween: 16,
            },
            577: {
                slidesPerView: '2',
                spaceBetween: 32,
            },
            769: {
                slidesPerView: '3',
                spaceBetween: 50,
            },
            1201: {
                slidesPerView: '3',
                spaceBetween: 70,
            }
        }
    })
}

